.design-container {
  padding: 5% 10%;
  text-align: center;

  h1 {
    color: white;
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }

  .controls {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;

    .search-bar {
      width: 200px;
      padding: 10px;
      font-size: 1rem;
      border: 1px solid #ddd;
      border-radius: 5px;
    }

    .sort-select {
      width: 250px;
      padding: 10px;
      font-size: 1rem;
      border: 1px solid #ddd;
      border-radius: 5px;
    }
  }

  .no-results {
    color: white;
    font-size: 1.5rem;
    margin-top: 2rem;
  }

  .category {
    margin-bottom: 3rem;

    h2 {
      color: #7dff00;
      font-size: 2rem;
      margin-bottom: 0.5rem;
    }

    .category-date {
      color: white;
      font-size: 1rem;
      margin-bottom: 1rem;
    }

    .design-images {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .image-wrapper {
        margin: 10px;
        width: 250px;
        height: 150px;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          cursor: pointer;
          transition: transform 0.3s ease;
        }

        &:hover img {
          transform: scale(1.1);
        }
      }
    }
  }
}